export interface NftApiType {
  identifier?: string;
  collection?: string;
  attributes?: string;
  nonce?: 19;
  type?: string;
  name?: string;
  creator?: string;
  royalties?: number;
  url?: string;
  media?: string;
  ticker?: string;
}

// build IPFS url
const buildIPFSUrl = (collItem: CollectionType): string => {
  let res = `${cfgCollection.apiPath}/${cfgCollection.collCID}`;
  res += collItem.image.replace("undefined", ""); //"image": "undefined/1.png",
  return res;
};

/**
 * Transform Json to NFT tags format
  {
    "trait_type": "Background",
    "value": "bluegreen1"
  }
  =>
  attr1:tr1;attr2:tr2
 */
const buildNFTTags = (collItem: CollectionType): string => {
  let res = "";
  for (const attr of collItem.attributes) {
    res += attr.trait_type + ":" + attr.value + ";";
  }
  return res;
};

