/**
 *
 * @param collItem
 * @returns
 */
const buildNftArgs = (collItem: CollectionType): TypedValue[] => {
  console.log("buildBMSNftArgs");

  // const collId: string = collIdentifier!;
  // // CDDD-29f066          :[434444442d323966303636]
  const tokenIdentifier: string = cfgCollection.tokenIdentifier!; // TODO take value from select list
  const colItemName: string = collItem.name!;
  const colItemRoyalties: number = cfgCollection.royalties!; //collectionItemRoyalties! * 100; // 100 = 1%
  const colItemTags: string = buildNFTTags(collItem);
  // const colItemQta: number = collectionItemQta! * 100;
  const colItemUrl: string = buildIPFSUrl(collItem);

  const hash = crypto.createHash("sha256");
  hash.update(colItemName);
  const colItemHash = hash.digest("hex");

  const args = [
    new TokenIdentifierValue(Buffer.from(tokenIdentifier, "utf8")), // colId
    new BigUIntValue(new BigNumber(1)), //amount
    BytesValue.fromUTF8(colItemName), //name
    new BigUIntValue(new BigNumber(cfgCollection.royalties)), //royalties
    BytesValue.fromUTF8(colItemHash), //hash
    BytesValue.fromUTF8(colItemTags), //attr
    BytesValue.fromUTF8(colItemUrl), //uri (www.nfts.com)
    BytesValue.fromUTF8(""), //uri (www.nfts.com)
    BytesValue.fromUTF8(""), //uri (www.nfts.com)
    new BigUIntValue(new BigNumber(3).multipliedBy(EGLD_DECI)), // withdraw_amount:
  ];
  return args;
};

/**
 *
 * @param value
 * @param functionName
 * @param gasLimitDefault
 * @param args
 * @returns
 */
const buildTransaction = (
  value: string,
  functionName: string,
  gasLimitDefault: number,
  args: TypedValue[],
): RawTransactionType => {
  console.log("buildBMSNftArgs");

  // const payload = new TransactionPayload("ping pong");
  const dataPayload = TransactionPayload.contractCall()
    .setFunction(new ContractFunction(functionName.toString()))
    .setArgs(args)
    .build();
  // gas
  const gasLimit = calculateGasLimit(gasLimitDefault, dataPayload);
  // transaction
  const pongTr: RawTransactionType = {
    receiver: contractAddress,
    data: dataPayload.toString(),
    value: value,
    gasLimit: gasLimit,
  };
  return pongTr;
};

/**
 * Create NFTs in a loop
 * @param nrNft
 * @param collItems
 * @returns
 */
export default function buildBMSNftCollTransaction(
  collItems: CollectionType[],
): RawTransactionType {
  console.log("buildBMSNftArgs");
  // first arg
  const tokenIdentifier: string = cfgCollection.tokenIdentifier!; // TODO take value from select list
  let args: TypedValue[] = [];
  args.push(new TokenIdentifierValue(Buffer.from(tokenIdentifier, "utf8"))); // colId

  // add N items from collection
  const nrNfts = collItems.length;
  for (let i = 0; i < nrNfts; i++) {
    const arg = buildNftArgs(collItems[i]);
    args = args.concat(arg);
  }

  // EGLD
  const value = Number(nrNfts) * Number(cfgCollection.nftPrice);

  // transacation
  const pongTr = buildTransaction(
    value.toString(),
    BMSFunction[BMSFunction.BMSNftColl],
    cfgFrmNft.minGasLimit.createNft,
    args,
  );
  return pongTr;
}

export function buildBMSNftTransaction(collItem: CollectionType) {
  console.log("buildBMSNftTransaction");
  // first arg
  const tokenIdentifier: string = cfgCollection.tokenIdentifier!; // TODO take value from select list
  const args = buildNftArgs(collItem);

  // EGLD
  const value = 1 * Number(cfgCollection.nftPrice);

  // transacation
  const pongTr = buildTransaction(
    value.toString(),
    BMSFunction[BMSFunction.BMSNft],
    cfgFrmNft.minGasLimit.createNft,
    args,
  );
  return pongTr;
}

//"$DISC_IDENTIFIER $DISC_AMOUNT $FUNCTION_2 $NFT_IDENTIFIER_LOC"
const buildEstdLookArgs = (estdAmount: number): TypedValue[] => {
  console.log("buildEstdLookArgs");

  const estdTokenIdentifier: string = cfgESTDtoken.tokenIdentifier!;
  const contractFunction = BMSFunction[BMSFunction.BMSEstdLook];
  const nftTokenIdentifier: string = cfgCollection.tokenIdentifier!;

  const args = [
    new TokenIdentifierValue(Buffer.from(estdTokenIdentifier, "utf8")), // estd token
    new BigUIntValue(new BigNumber(estdAmount)), //amount
    BytesValue.fromUTF8(contractFunction), // function
    new TokenIdentifierValue(Buffer.from(nftTokenIdentifier, "utf8")), // nft token
  ];
  return args;
};

export function buildBMSEstdLookTransaction(estdAmount: number) {
  console.log("buildBMSNftTransaction");

  const args = buildEstdLookArgs(estdAmount);

  // transacation
  const pongTr = buildTransaction(
    "0.0",
    BMSFunction[BMSFunction.ESDTTransfer],
    cfgFrmNft.minGasLimit.transferEstdLook,
    args,
  );
  return pongTr;
}
