/////////////////////////////
export enum BMSFunction {
  getBSMCount,
  getBMSEstdLook,
}

/////////////////////////////

/**
 * QUERY current NFT collection counter
 * @returns
 */
export default function buildQueryBSMCount(): Query {
  const query = new Query({
    address: new Address(contractAddress),
    func: new ContractFunction(BMSFunction[BMSFunction.getBSMCount]),
    args: [
      new TokenIdentifierValue(
        Buffer.from(cfgCollection.tokenIdentifier, "utf8"),
      ),
    ],
    // args: [new AddressValue(new Address(address))],
  });
  return query;
}

/**
 * QUERY current ESTD looked tokens
 * @returns
 */
export function buildQueryGetBMSEstdLook(addressWallet: string): Query {
  const query = new Query({
    address: new Address(contractAddress),
    func: new ContractFunction(BMSFunction[BMSFunction.getBMSEstdLook]),
    args: [
      new TokenIdentifierValue(
        Buffer.from(cfgCollection.tokenIdentifier, "utf8"),
      ),
      new AddressValue(new Address(addressWallet)),
    ],
    // args: [new AddressValue(new Address(address))],
  });
  return query;
}

/**
 *
 * @param result
 * @returns
 */
export function bmsNftReturnDataToNumer(
  result: QueryResponse,
): number | undefined {
  const [encoded] = result.returnData;
  console.log(result);

  switch (encoded) {
    case undefined:
      // setHasPing(true);
      break;
    case "":
      return 0;
    // break;
    default: {
      const decoded = Buffer.from(encoded, "base64").toString("hex");
      const count = parseInt(decoded, 16);
      console.log("done");
      return count;
      // break;
    }
  }
  console.log("done");
}

function decodeBase64ToString(encoded: string) {
  return Buffer.from(encoded, "base64").toString();
}

function decodeBase64ToNumber(encoded: string) {
  return parseInt(decodeBase64Hex(encoded), 16);
}

function decodeBase64Hex(encoded: string) {
  return Buffer.from(encoded, "base64").toString("hex");
}

/**
 * Convert bmsEstdLook QueryResponce to SimpleTokenType
 * @param result
 * @returns
 */
export function bmsEstdLook2SimpleTokenType(
  result: QueryResponse,
): SimpleTokenType | null {
  console.log(result);

  if (result === undefined || result === null) {
    return null;
  }
  const token: SimpleTokenType = {
    identifier: decodeBase64ToString(result.returnData[0]),
    nonce: decodeBase64ToNumber(result.returnData[1]),
    amount: decodeBase64ToNumber(result.returnData[2]),
  };

  console.log(token);
  return token;
}

